.page-container {
  width: 320px;
  margin: 0 auto;
  // border: solid 5px red;
}

@media (min-width: @tablet-width) {
  .page-container {
    width: @tablet-width;
    margin: 0 auto;
  }
}

@media (min-width: @dekstop-width) {
  .page-container {
    width: @dekstop-width;
    margin: 0 auto;
    position: relative;

    &--main::before {
      content: url("../img/dekstop-main-section-background.jpg");
      position: absolute;
      width: 50%;
      height: 694px;
      right: 0;
      left: auto;
      top: 0;
    }
  }
}
